import React from "react";
import * as MSPs from "../Data/MSPs";
import * as MSP from "../DTOs/MSP";
import "../App.css";
import BootstrapTable from "react-bootstrap-table-next";
import filterFactory, { textFilter } from "react-bootstrap-table2-filter";
import * as Candidates from "../Data/Candidates";

interface ITablePageState {
  IncumbentsSelected: boolean;
}

class TablePage extends React.Component<{}, ITablePageState> {
  public constructor(props: {}) {
    super(props);

    this.state = {
      IncumbentsSelected: true
    };
  }

  public render(): JSX.Element {
    const data = MSPs.MSPs.MSPsCollection.filter(m => !m.StandingDown);
    const dataMap = data.map(d => ({
      name: this.FormatName(d.Name, true, d.Support),
      party: this.CamelCaseToSentence(MSP.Party[d.Party]),
      haveBeenContacted: d.HaveBeenContacted,
      support: d.Support,
      region: this.CamelCaseToSentence(MSP.Region[d.Region]),
      constituency: d.Constituency
        ? this.CamelCaseToSentence(MSP.Constituency[d.Constituency])
        : null,
      statement: d.Statement
    }));

    const data2 = MSPs.MSPs.MSPsCollection.filter(m => m.StandingDown);
    const dataMap2 = data2.map(d => ({
      name: this.FormatName(d.Name, true, d.Support),
      party: this.CamelCaseToSentence(MSP.Party[d.Party]),
      region: this.CamelCaseToSentence(MSP.Region[d.Region]),
      constituency: d.Constituency
        ? this.CamelCaseToSentence(MSP.Constituency[d.Constituency])
        : null
    }));

    const data3 = Candidates.MSPs.MSPsCollection.filter(m => !m.StandingDown);
    const dataMap3 = data3
      .map(d => ({
        name: this.FormatName(d.Name, false, d.Support),
        party: this.CamelCaseToSentence(MSP.Party[d.Party]),
        haveBeenContacted: d.HaveBeenContacted,
        support: d.Support,
        region: this.CamelCaseToSentence(MSP.Region[d.Region]),
        constituency: d.Constituency
          ? this.CamelCaseToSentence(MSP.Constituency[d.Constituency])
          : null,
        statement: d.Statement
      }))
      .concat(dataMap)
      .sort((n1, n2) => {
        if (n1.name > n2.name) {
          return 1;
        }

        if (n1.name < n2.name) {
          return -1;
        }

        return 0;
      });

    const columns = [
      {
        dataField: "name",
        text: "Name",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "party",
        text: "Party",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "haveBeenContacted",
        text: "Have Been Contacted",
        sort: true,
        formatter: (value: boolean): JSX.Element => {
          return this.FormatContacted(value);
        }
      },
      {
        dataField: "support",
        text: "Support",
        sort: true,
        formatter: (value: MSP.Support): JSX.Element => {
          return this.FormatSupport(value);
        }
      },
      {
        dataField: "region",
        text: "Region",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "constituency",
        text: "Constituency",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      { dataField: "statement", text: "Statement" }
    ];

    const columns2 = [
      {
        dataField: "name",
        text: "Name",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "party",
        text: "Party",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "region",
        text: "Region",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "constituency",
        text: "Constituency",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      }
    ];

    const columns3 = [
      {
        dataField: "name",
        text: "Name",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "party",
        text: "Party",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "support",
        text: "Support",
        sort: true,
        formatter: (value: MSP.Support): JSX.Element => {
          return this.FormatSupport(value);
        }
      },
      {
        dataField: "region",
        text: "Region",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "constituency",
        text: "Constituency",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      { dataField: "statement", text: "Statement" }
    ];

    return (
      <div className="App main-table">
        <p>
          <b>
            Are you an MSP or candidate who wants to make sure your views are
            represented here?
          </b>
        </p>
        <p>
          Read our <a href={"/request"}>request</a> and{" "}
          <a href="mailto:endconversiontherapyscotland@gmail.com?subject=Candidate Reply">
            email us
          </a>{" "}
          and we'll make sure your response is listed here.
        </p>
        <p>
          If we know an email has been sent to an MSP by a constituent they will
          have "Have Been Contacted" confirmed below.
        </p>
        <div className="select-buttons" style={{ display: "none" }}>
          <button
            disabled={!this.state.IncumbentsSelected}
            onClick={this.SelectAllCandidates}
          >
            All Candidates
          </button>
          <button
            disabled={this.state.IncumbentsSelected}
            onClick={this.SelectIncumbents}
          >
            Incumbent MSPs
          </button>
        </div>
        {this.state.IncumbentsSelected && (
          <>
            <h1>Incumbent MSPs</h1>
            <BootstrapTable
              keyField="name"
              data={dataMap}
              columns={columns}
              bordered={true}
              filter={filterFactory()}
            />

            <h1 style={{ maxWidth: "1000px" }}>Incumbent MSPs Standing Down</h1>
            <BootstrapTable
              keyField="name"
              data={dataMap2}
              columns={columns2}
              bordered={true}
              filter={filterFactory()}
            />
          </>
        )}
        {!this.state.IncumbentsSelected && (
          <>
            <h1>Candidates</h1>
            <h3>Incumbent MSPs are in bold</h3>
            <BootstrapTable
              keyField="name"
              data={dataMap3}
              columns={columns3}
              bordered={true}
              filter={filterFactory()}
            />
          </>
        )}
      </div>
    );
  }

  private FormatContacted(value: boolean): JSX.Element {
    switch (value) {
      case true:
        return <b style={{ margin: 0, color: "#06a800" }}>{"Yes"}</b>;
      case false:
        return <p style={{ margin: 0, color: "#333333" }}>{"No"}</p>;
    }
  }

  private FormatSupport(value: MSP.Support): JSX.Element {
    switch (value) {
      case MSP.Support.NoReply:
        return (
          <p style={{ margin: 0, color: "#555555" }}>
            {this.CamelCaseToSentence(MSP.Support[value])}
          </p>
        );
      case MSP.Support.No:
        return (
          <p style={{ margin: 0, color: "#a80000", fontWeight: "bold" }}>
            {this.CamelCaseToSentence(MSP.Support[value])}
          </p>
        );
      case MSP.Support.Yes:
        return (
          <p style={{ margin: 0, color: "#06a800", fontWeight: "bold" }}>
            {this.CamelCaseToSentence(MSP.Support[value])}
          </p>
        );
      case MSP.Support.YesWithCaveats:
        return (
          <p style={{ margin: 0, color: "#EE9900", fontWeight: "bold" }}>
            {this.CamelCaseToSentence(MSP.Support[value])}
          </p>
        );
      default:
        return <></>;
    }
  }

  private CamelCaseToSentence(passed: string) {
    const result = passed.replace(/([A-Z])/g, " $1");
    let final = result.charAt(0).toUpperCase() + result.slice(1);
    final = final.replace("And", "and");
    return final;
  }

  private FormatName(passed: string, incumbent: boolean, support: MSP.Support) {
    let text = passed;
    if (passed.includes(",")) {
      const names = passed.split(",");
      text = names[1] + " " + names[0];
    }
    if (incumbent) {
      switch (support) {
        case MSP.Support.NoReply:
          return (
            <p style={{ margin: 0, color: "#555555", fontWeight: "bold" }}>
              {text}
            </p>
          );
        case MSP.Support.No:
          return (
            <p style={{ margin: 0, color: "#a80000", fontWeight: "bold" }}>
              {text}
            </p>
          );
        case MSP.Support.Yes:
          return (
            <p style={{ margin: 0, color: "#06a800", fontWeight: "bold" }}>
              {text}
            </p>
          );
        case MSP.Support.YesWithCaveats:
          return (
            <p style={{ margin: 0, color: "#EE9900", fontWeight: "bold" }}>
              {text}
            </p>
          );
        default:
          return <></>;
      }
    } else {
      switch (support) {
        case MSP.Support.NoReply:
          return <p style={{ margin: 0, color: "#555555" }}>{text}</p>;
        case MSP.Support.No:
          return <p style={{ margin: 0, color: "#a80000" }}>{text}</p>;
        case MSP.Support.Yes:
          return <p style={{ margin: 0, color: "#06a800" }}>{text}</p>;
        case MSP.Support.YesWithCaveats:
          return <p style={{ margin: 0, color: "#EE9900" }}>{text}</p>;
        default:
          return <></>;
      }
    }
  }

  private SelectAllCandidates = () => {
    this.setState({ IncumbentsSelected: false });
  };

  private SelectIncumbents = () => {
    this.setState({ IncumbentsSelected: true });
  };
}

export default TablePage;
